import React,{useState} from 'react'

/*
This component allows us to insert a new comments onto the site and stores it in the mongoDB database for our articles
*/
const AddCommentForm = ({articleName, setArticleInfo}) => {
    const [username, setUsername] = useState('');
    const [comment, setComment] = useState('');

    //Code that adds a comment to the data base when the user submits
    const addComment = async () => {
        const result = await fetch(`/api/articles/${articleName}/add-comment`, {
            method: 'post',
            body: JSON.stringify({ username, text: comment }),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body = await result.json();
        setArticleInfo(body)
        setUsername('');
        setComment('');
    }
    
    return (
        <div id="add-comment-form">
            <h3>Add a Comment</h3>
            <label>
                Name:
                <input
                    type="text"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}/>
            </label>
            <label>
                Comment:
                <textarea
                    rows="4"
                    cols="50"
                    value={comment}
                    onChange={(e) => setComment(e.target.value)}/>
            </label>
            <button onClick={() => addComment()}>Add Comment</button>
        </div>
    )
}

export default AddCommentForm;
