import React from 'react'

const CommentsList = ({ articleName, setArticleInfo, comments }) => {
    
    const clearComments = async () => {
        const result = await fetch(`/api/articles/${articleName}/delete-all-comments`, {
            method: 'post',  
        });
        const body = await result.json();
        setArticleInfo(body)
    }

    const removeComment = async (commentIdToRemove) => {
        const result = await fetch(`/api/articles/${articleName}/delete-comment`, {
            method: 'post',
            body: JSON.stringify({ targetId: commentIdToRemove }),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body = await result.json();
        setArticleInfo(body)
    }

    if (comments.length === 0) {
        return <h3>There are no comments on this article.</h3>
    }
    return (
        <>
        <h3>Comments:</h3>
        {comments.map((comment, key) => {
            return (
                <div className="comment" key={key}>
                    <h4>{comment.username}</h4>
                    <p>{comment.text}</p>
                    <button onClick={() => removeComment(comment.commentId)}>Remove comment</button>
                </div>
            )
        })}
        <button onClick={clearComments}>Clear Comments</button>
        </>
    )
}

export default CommentsList;
